$cell-size: 300px;

body {
  margin: 0;
}

.container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax($cell-size, 1fr));
  grid-template-rows: repeat(6, $cell-size);
}

@media (min-width: 1200px) {
  .container {
    grid-template-columns: repeat(3, 1fr);
  }
}

.box {
  color: white;
  font-size: 100px;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  line-height: $cell-size;

  $colors:
    "1" rgb(100%, 0, 0),
    "2" rgb(80%, 0, 0),
    "3" rgb(60%, 0, 0),
    "4" rgb(40%, 0, 0),
    "5" rgb(20%, 0, 0),
    "6" rgb(0, 0, 0);

  @each $num, $color in $colors {
    &:nth-child(#{$num}) {
      background-color: $color;
    }
  }

  @for $number from 1 through 6 {
    &:nth-child(#{$number})::before {
      content: "#{$number}";
    }
  }
}
